<template>
  <section class="build-product-container">
    <div class="box-container">
      <div style="margin-bottom:20px;">
        <el-button type="primary" size="small" @click="addSNProduct"></el-button>
      </div>
      <!---->
      <el-col :span="24" class="toolbar" style="padding-bottom: 0px;margin-bottom: 0">
        <el-form :inline="true" :model="filters" label-width="100px">
          <el-form-item :size="'small'" label="">
            <el-select
                v-model="filters.status"
                placeholder=""
                style="width: 200px">
              <el-option
                  v-for="item in productList"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :size="'small'" label="">
            <el-select
                v-model="filters.giftType"
                placeholder=""
                style="width: 200px">
              <el-option
                  v-for="item in productTypeList"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :size="'small'" label="">
            <el-select
                v-model="filters.hasDelivery"
                placeholder=""
                style="width: 200px">
              <el-option
                  v-for="item in deliveryList"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :size="'small'" label="">
            <el-select
                v-model="filters.cateLvl1"
                placeholder=""
                @change="selectBrand1"
                style="width: 200px">
              <el-option
                  v-for="item in cateLvl1List"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
              </el-option>
            </el-select>
            <el-select
                v-model="filters.cateLvl2"
                placeholder=""
                @change="selectBrand2"
                style="width: 200px">
              <el-option
                  v-for="item in cateLvl2List"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
              </el-option>
            </el-select>
            <el-select
                v-model="filters.cateLvl3"
                placeholder=""
                @change="selectBrand3"
                style="width: 200px">
              <el-option
                  v-for="item in cateLvl3List"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
              </el-option>
            </el-select>
            <el-select
                v-model="filters.cateLvl4"
                placeholder=""
                style="width: 200px">
              <el-option
                  v-for="item in cateLvl4List"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :size="'small'" label="">
            <el-input v-model="filters.keywords" style="width: 200px" placeholder=""></el-input>
          </el-form-item>
          <div></div>
          <el-form-item class="mr0" :size="'small'">
            <el-button type="primary" size="small" @click="commitForm"></el-button>
            <el-button size="small" class="important" @click="getStatus"></el-button>
            <el-button type="primary" size="small" @click="sortBySales"></el-button>
            <el-button type="primary" size="small" @click="sortByScore"></el-button>
            <el-button type="primary" size="small" @click="sortByTime"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </div>
    <div class="box-container">
      <!---->
      <el-col class="crm-table-wrap" v-loading="listLoading" :span="24">
        <el-table
            :data="listJD"
            @selection-change="handleSelectionChange"
            style="width: 100%">
          <el-table-column
              type="selection"
              width="55">
          </el-table-column>
          <el-table-column
              type="index"
              label=""
              width="50">
          </el-table-column>
          <el-table-column
              prop="productId"
              label="ID"
              width="200">
          </el-table-column>
          <el-table-column
              prop="memo"
              label=""
              width="120">
          </el-table-column>
          <el-table-column
              prop="giftType"
              label="">
            <template slot-scope="scope">
              <span>{{ scope.row.giftType == "1" ? "":""}}</span>
            </template>
          </el-table-column>
          <el-table-column
              prop="cate1Name"
              label=""
              width="120">
          </el-table-column>
          <el-table-column
              prop="cate2Name"
              label=""
              width="120">
          </el-table-column>
          <el-table-column
              prop="cate3Name"
              label=""
              width="120">
          </el-table-column>
          <el-table-column
              prop="cate4Name"
              label=""
              width="120">
          </el-table-column>
          <el-table-column
              prop="price"
              label="">
          </el-table-column>
          <el-table-column
              prop="deliveryScore"
              label=""
              width="120">
          </el-table-column>
          <el-table-column
              prop="score"
              label="">
          </el-table-column>
          <el-table-column
              prop="shopQuantity"
              label="">
          </el-table-column>
          <el-table-column
              prop="createTime"
              label=""
              width="200">
            <template slot-scope="scope">
              <span>{{ new Date(scope.row.createTime).Format('yyyy-MM-dd hh:mm:ss')}}</span>
            </template>
          </el-table-column>
          <el-table-column
              prop="status"
              label="">
            <template slot-scope="scope">
              <span>{{ scope.row.status == "1" ? "":""}}</span>
            </template>
          </el-table-column>
          <el-table-column
              fixed="right"
              label=""
              width="240"
          >
            <template slot-scope="scope" >
              <el-button
                  size="mini"
                  @click="editSNProduct(scope.row.id)"
              ></el-button>
              <el-button
                  v-if="scope.row.status == '1'"
                  size="mini"
                  @click="productOffline(scope.row.id)"
              ></el-button>
              <el-button
                  v-else
                  size="mini"
                  @click="productOnline(scope.row.id)"
              ></el-button>
              <el-button
                  size="mini"
                  type="danger"
                  @click="deleteProduct(scope.row.id)"
              ></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-col>

      <!---->
      <el-col :span="24" class="footer-toolbar">
        <el-button
            type="primary"
            size="small"
            :disabled="batchDelete"
            @click="removeBatch(ids)"
        ></el-button>
        <el-button type="primary" class="btn-jump-to" @click="jumpTo"></el-button>
        <el-pagination
            background
            layout="total,prev, pager, next,jumper"
            :current-page="currentPage"
            @current-change="handleCurrentChange"
            :page-size="pageSize"
            :total="total"
            style="float:right;">
        </el-pagination>
      </el-col>
    </div>
  </section>
</template>

<script>
  export default {
    name: "BuildProduct",
    data() {
      return {
        //
        listLoading:false,
        //
        productList:[
          {name: "", id: ""}
        ],
        //
        productTypeList:[
          {name: "", id: ""}
        ],
        //
        deliveryList:[
          {name: "", id: ""},
          {name: "", id: "1"},
          {name: "", id: "0"}
        ],
        //
        cateLvl1List:[
          {name: "", id: ""}
        ],
        cateLvl2List:[
          {name: "", id: ""}
        ],
        cateLvl3List:[
          {name: "", id: ""}
        ],
        cateLvl4List:[
          {name: "", id: ""}
        ],
        filters:{
          //
          status:"",
          //
          giftType:"",
          //
          hasDelivery:"",
          //
          keywords:"",
          //
          cateLvl1:"",
          cateLvl2:"",
          cateLvl3:"",
          cateLvl4:"",
        },
        //
        listJD:[],
        //10
        sort:0,
        //
        ids:"",
        //
        pageSize:10,

        pageNo: 1,
        //
        currentPage:1,
        //
        total:0,

        //
        batchDelete:true,
      }
    },
    created(){
      this.getProductList()
      this.getProductType()
      this.getOneCategory()
      this.getListSN()
    },
    methods:{
      //
      getProductList(){
        this.$request.post('/sc/saotx/mall/statusMap',{}, true, (res) => {
          if (res.ret == '200000') {
            this.productList = [...this.productList,...res.data]
          }
        })
      },
      //
      getProductType(){
        this.$request.post('/sc/saotx/mall/giftTypeMap',{}, true, (res) => {
          if (res.ret == '200000') {
            this.productTypeList = [...this.productTypeList,...res.data]
          }
        })
      },
      //1
      getOneCategory(){
        this.$request.post('/sc/saotx/mall/cate/oneCategory',{}, true, (res) => {
          if (res.ret == '200000') {
            this.cateLvl1List = [...this.cateLvl1List,...res.data]
          }
        })
      },
      selectBrand1(){
        this.filters.cateLvl2 = ""
        this.cateLvl2List = [{name: "", id: ""}]
        this.filters.cateLvl3 = ""
        this.cateLvl3List = [{name: "", id: ""}]
        this.filters.cateLvl4 = ""
        this.cateLvl4List = [{name: "", id: ""}]
        this.getTwoCategory()
      },
      //2
      getTwoCategory(){
        if(this.filters.cateLvl1 == "")return
        this.$request.post('/sc/saotx/mall/cate/moreCategory',{id:this.filters.cateLvl1}, true, (res) => {
          if (res.ret == '200000') {
            this.cateLvl2List = [...this.cateLvl2List,...res.data]
          }
        })
      },
      selectBrand2(){
        this.filters.cateLvl3 = ""
        this.cateLvl3List = [{name: "", id: ""}]
        this.filters.cateLvl4 = ""
        this.cateLvl4List = [{name: "", id: ""}]
        this.getThreeCategory()
      },
      //3
      getThreeCategory(){
        if(this.filters.cateLvl2 == "")return
        this.$request.post('/sc/saotx/mall/cate/moreCategory',{id:this.filters.cateLvl2}, true, (res) => {
          if (res.ret == '200000') {
            this.cateLvl3List = [...this.cateLvl3List,...res.data]
          }
        })
      },
      selectBrand3(){
        this.filters.cateLvl4 = ""
        this.cateLvl4List = [{name: "", id: ""}]
        this.getFourCategory()
      },
      //4
      getFourCategory(){
        this.$request.post('/sc/saotx/mall/cate/moreCategory',{id:this.filters.cateLvl3}, true, (res) => {
          if (res.ret == '200000') {
            this.cateLvl4List = [...this.cateLvl4List,...res.data]
          }
        })
      },
      //
      modiifySort(){
        if(this.sort == 1){
          this.sort = 0
        }else {
          this.sort = 1
        }
      },
      //
      getListSN(type) {
        let params = {
          //
          status:this.filters.status,
          //
          giftType:this.filters.giftType,
          //
          hasDelivery:this.filters.hasDelivery,
          //
          keywords:this.filters.keywords,
          //
          cateLvl1:this.filters.cateLvl1,
          cateLvl2:this.filters.cateLvl2,
          cateLvl3:this.filters.cateLvl3,
          cateLvl4:this.filters.cateLvl4,

          pageSize:this.pageSize,
          pageNo:this.pageNo
        };
        if(type == "bySales"){
          params.order = 1
          params.sort = this.sort
          this.modiifySort()
        }else if(type == "byScore"){
          params.order = 2
          params.sort = this.sort
          this.modiifySort()
        }else if(type == "byTime"){
          params.order = 3
          params.sort = this.sort
          this.modiifySort()
        }
        this.postSearch(params)
      },
      postSearch(params) {
        this.listLoading = true;
        this.$request.post('/sc/mall/suning/product/list', params, true, (res) => {
          if (res.ret == '200000') {
            console.log(res.data)
            this.listLoading = false;
            this.listJD = res.data.list
            this.total = res.data.page.count
            this.pageNo = res.data.page.pageNo
          }
        })
      },
      //radio,
      getStatus() {
        //
        this.filters.status = ''
        //
        this.filters.giftType = ''
        //
        this.filters.keywords = ''
        //
        this.filters.cateLvl1 = ''
        this.filters.cateLvl2 = ''
        this.filters.cateLvl3 = ''
        this.filters.cateLvl4 = ''

        this.pageNo = 1
        this.currentPage = 1
        this.getListSN()
      },
      //
      commitForm() {
        this.pageNo = 1
        this.currentPage = 1
        this.getListSN()
      },
      //
      sortBySales(){
        this.getListSN("bySales")
      },
      sortByScore(){
        this.getListSN("byScore")
      },
      sortByTime(){
        this.getListSN("byTime")
      },
      addSNProduct(){
        this.$router.push({
          path:'/mall/product/sn/addSNProduct'
        })
      },
      editSNProduct(id){
        this.$router.push({
          path:'/mall/product/sn/editSNProduct?id='+id
        })
      },
      //
      deleteProduct(id){
        let params = {id}
        this.$confirm(``, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
        this.$request.post('/sc/saotx/mall/product/remove',params,true,res => {
          if(res.ret == "200000"){
            this.$message({
              message: '',
              type: 'success'
            });
            this.getListSN()
          }else{
            this.$message({
              message: res.message,
              type: 'warning'
            });
          }
        })
        })
      },
      //
      productOnline(id){
        let params = {id}
        this.$request.post('/sc/saotx/mall/product/onlineSuning',params,true,res => {
          if(res.ret == "200000"){
            this.$message({
              message: '',
              type: 'success'
            });
            this.getListSN()
          }else{
            this.$message({
              message: res.message,
              type: 'warning'
            });
          }
        })
      },//
      productOffline(id){
        let params = {id}
        this.$request.post('/sc/saotx/mall/product/offline',params,true,res => {
          if(res.ret == "200000"){
            this.$message({
              message: '',
              type: 'success'
            });
            this.getListSN()
          }else{
            this.$message({
              message: res.message,
              type: 'warning'
            });
          }
        })
      },
      handleSelectionChange(val) {
        let arr = val
        console.log(arr)
        let idsArr = []
        if(arr.length > 0){
          this.batchDelete = false
          arr.map((e,i) => {
            idsArr.push(e.id)
          })
          this.ids = idsArr.join(",")
        }else{
          this.ids = ""
          this.batchDelete = true
        }
      },
      //
      removeBatch(){
        this.$confirm(``, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$request.post('/sc/saotx/mall/product/removeBatch',{ids:this.ids},true,res => {
            if(res.ret == "200000"){
              this.$message({
                message: '',
                type: 'success'
              });
              this.getListSN()
            }else{
              this.$message({
                message: res.message,
                type: 'warning'
              });
            }
          })
        })
      },
      //
      jumpTo() {
        this.$emit('current-change')
      },
      //
      handleCurrentChange(val) {
        this.pageNo = val
        this.currentPage = val
        this.getListSN()
      },
    }
  }
</script>

<style lang="scss" scoped>
 .build-product-container{
   .box-container{
     background-color: #fff;
     padding: 15px;
     margin-bottom: 30px;
     &:after{
       content: '';
       display: block;
       width: 0;
       height: 0;
       clear: both;
     }
   }
 }
 .el-form-item {
    margin-bottom: 20px;
 }
</style>
<style>
  .build-product-container .el-table{text-align: center}
  .build-product-container .el-table th{text-align: center}
  .build-product-container .el-table td{text-align: center}
  .build-product-container .mini .el-form-item__label {width: 40px!important;}
</style>


