<template>
  <div class="audit-manage-container">
    <div class="box-container">
      <!---->
      <el-col :span="24" class="toolbar" style="padding-bottom: 0px;margin-bottom: 0">
        <el-form :inline="true" :model="filters" label-width="80px">
          <el-form-item :size="'small'" label="">
            <el-select
                v-model="filters.jdOrderStatus"
                placeholder=""
                style="width: 200px">
              <el-option
                  v-for="item in jdOrderStatusList"
                  :key="item.code"
                  :label="item.name"
                  :value="item.code">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :size="'small'" label="">
            <el-date-picker
                v-model="filters.startTime"
                type="datetime"
                placeholder=""
                value-format="yyyy-MM-dd HH:mm"
                style="width: 200px">
            </el-date-picker>
          </el-form-item>
          <el-form-item :size="'small'" label="" class="mini">
            <el-date-picker
                v-model="filters.endTime"
                type="datetime"
                placeholder=""
                value-format="yyyy-MM-dd HH:mm"
                style="width: 200px">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="">
            <el-input size="small"  v-model="filters.userName"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input size="small"  v-model="filters.mobile"></el-input>
          </el-form-item>
          <div></div>
          <el-form-item class="mr0" :size="'small'">
            <el-button type="primary" size="small" @click="commitForm"></el-button>
            <el-button size="small" class="important" @click="getStatus"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </div>
    <div class="box-container">
      <!---->
      <el-col class="crm-table-wrap" v-loading="listLoading" :span="24">
        <el-table
            :data="authList"
            @selection-change="handleSelectionChange"
            style="width: 100%">
          <el-table-column
              type="selection"
              width="55">
          </el-table-column>
          <el-table-column
              type="index"
              label=""
              width="100">
          </el-table-column>
          <el-table-column
              prop="jdOrderStatus"
              label="">
            <template slot-scope="scope">
              <span v-if="scope.row.jdOrderStatus == 0">{{ '' }}</span>
              <span v-else-if="scope.row.jdOrderStatus == 1">{{ '' }}</span>
              <span v-else>{{ '' }}</span>
            </template>
          </el-table-column>
          <el-table-column
              prop="orderId"
              label=""
              width="300">
          </el-table-column>
          <el-table-column
              prop="awardName"
              label="">
          </el-table-column>
          <el-table-column
              prop="score"
              label="">
          </el-table-column>
          <el-table-column
              prop="originScore"
              label="">
          </el-table-column>
          <el-table-column
              prop="openId"
              label="opid"
              width="300">
          </el-table-column>
          <el-table-column
              prop="userName"
              label=""
              width="100px">
          </el-table-column
          ><el-table-column
              prop="mobile"
              label="">
          </el-table-column>
          <el-table-column
              prop="mobile"
              label=""
              width="300">
            <template slot-scope="scope">
              <span>{{ scope.row.provinceName + scope.row.cityName + scope.row.countryName + scope.row.address  }}</span>
            </template>
          </el-table-column>
          <el-table-column
              prop="createTime"
              label=""
              width="200">
            <template slot-scope="scope">
              <span>{{ new Date(scope.row.createTime).Format('yyyy-MM-dd hh:mm:ss')}}</span>
            </template>
          </el-table-column>


          <el-table-column
              fixed="right"
              label=""
              width="240"
          >
            <template slot-scope="scope" >
              <el-button
                  v-if="scope.row.jdOrderStatus == 0"
                  size="mini"
                  @click="modifyAuthOrder(scope.row.id,'1')"
              ></el-button>
              <el-button
                  v-if="scope.row.jdOrderStatus == 0"
                  size="mini"
                  type="danger"
                  @click="modifyAuthOrder(scope.row.id,'2')"
              ></el-button>
              <span v-else></span>
            </template>
          </el-table-column>
        </el-table>
      </el-col>

      <!---->
      <el-col :span="24" class="footer-toolbar">
        <el-button
            type="primary"
            size="small"
            :disabled="batchOperations"
            @click="modifyAuthOrder(ids,'1',true)"
        ></el-button>
        <el-button type="primary" class="btn-jump-to" @click="jumpTo"></el-button>
        <el-pagination
            background
            layout="total,prev, pager, next,jumper"
            :current-page="currentPage"
            @current-change="handleCurrentChange"
            :page-size="pageSize"
            :total="total"
            style="float:right;">
        </el-pagination>
      </el-col>
    </div>
  </div>
</template>

<style lang="scss" scoped>
  .audit-manage-container{
    .box-container{
      background-color: #fff;
      padding: 15px;
      margin-bottom: 30px;
      &:after{
        content: '';
        display: block;
        width: 0;
        height: 0;
        clear: both;
      }
    }
  }
  .el-form-item {
    margin-bottom: 20px;
  }
</style>
<style>
  .audit-manage-container .el-table{text-align: center}
  .audit-manage-container .el-table th{text-align: center}
  .audit-manage-container .el-table td{text-align: center}
  .audit-manage-container .mini .el-form-item__label {width: 40px!important;}
</style>

<script>
  export default {
    data() {
      return {
        //
        listLoading:false,
        //
        batchOperations:true,
        //
        ids:'',
        //
        jdOrderStatusList:[
          {
            code:"",
            name:""
          },
          {
            code:"0",
            name:""
          },
          {
            code:"1",
            name:""
          },
          {
            code:"2",
            name:""
          }
        ],
        filters: {
          //0-1-2-
          jdOrderStatus :'0',
          //
          startTime:'',
          //
          endTime:'',
          mobile:'',
          userName:''

        },
        //
        authList:[],

        //
        pageSize:10,

        pageNo: 1,
        //
        currentPage:1,
        //
        total:0
      }
    },
    methods:{
      //
      getAuthList() {
        let params = {
          jdOrderStatus: this.filters.jdOrderStatus,
          startTime: this.filters.startTime,
          endTime: this.filters.endTime,
          pageNo: this.pageNo,
          pageSize: this.pageSize,
          mobile:this.filters.mobile,
          userName:this.filters.userName,
          orderType:4    //
        };
        this.postSearch(params)
      },


      postSearch(params) {
        this.listLoading = true;
        this.$request.post('/sc/saotx/mall/order/authList', params, true, (res) => {
          if (res.ret == '200000') {
            console.log(res.data)
            this.listLoading = false;
            this.authList = res.data.list
            this.total = res.data.page.count
            this.pageNo = res.data.page.pageNo
          }
        })
      },
      //
      commitForm() {
        this.pageNo = 1
        this.currentPage = 1
        this.getAuthList()
      },
      //radio,
      getStatus() {
        this.filters.jdOrderStatus = ''
        this.filters.startTime = ''
        this.filters.endTime = ''
        this.filters.userName = ''
        this.filters.mobile = ''

        this.pageNo = 1
        this.currentPage = 1
        this.getAuthList()
      },
      //
      handleEdit(index, row) {
        this.$router.push({path:'/product/editSupplier?id='+row.id})
      },
      //
      handleCurrentChange(val) {
        this.pageNo = val
        this.currentPage = val
        this.getAuthList()
      },
      //
      jumpTo() {
        this.$emit('current-change')
      },
      //title
      // istitle(value) {
      //   if (value) {
      //     if (value.length > 5) {
      //       return value
      //     } else {
      //       return ""
      //     }
      //   }
      // },
      //
      modifyAuthOrder(ids,jdOrderStatus,batch) {
        let params = {
          ids,
          jdOrderStatus
        }
        let flag = ""
        if(jdOrderStatus == '2'){
          flag = ""
        }
        let _batch = ""
        if(batch){
          _batch = ""
        }
        this.$confirm(`${flag}${_batch}`, '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            // this.$request.post('/sc/saotx/mall/order/authOrder',params,true,res => {
              this.$request.post('/sc/saotx/mall/order/authOrder/suning',params,true,res => {
            if(res.ret == "200000"){
              this.$message({
                message: '',
                type: 'success'
              });
              this.getStatus()
            }else{
              this.$message({
                message: res.message,
                type: 'warning'
              });
            }
            })
        })
      },
      handleSelectionChange(val) {
        let arr = val
        console.log(arr)
        let idsArr = []
        if(arr.length > 0 && this.filters.jdOrderStatus == "0"){
          this.batchOperations = false
          arr.map((e,i) => {
            idsArr.push(e.id)
          })
          this.ids = idsArr.join(",")
        }else{
          this.ids = ""
          this.batchOperations = true
        }

      }

    },
    created(){
      this.getAuthList()
    }
  }
</script>



